// Variables
$neon: #2196f3;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100vh;
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  font-family: sans-serif;
  font-family: "Chelsea Market", cursive;
  overflow-x: hidden;
}

// Section Title
.section-title {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 8px;
  border-bottom: 2px solid tan;
  width: 30%;
  margin: 40px auto;
}

@media (max-width: 875px) {
  .section-title {
    width: auto;
  }
}

// Header
.header {
  .header-title {
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 3rem;
    margin-top: 20px;
  }
}

// Navbar
nav {
  width: 100%;

  ul {
    list-style: none;
    display: flex;
    justify-content: center;
    transition: 0.5s;

    li {
      padding: 15px;
      cursor: pointer;
      letter-spacing: 2px;

      a {
        text-decoration: none;
        color: #000;
        &:hover {
          color: tan;
          transition: 0.4s;
        }
      }
    }
  }
}

//Wrapper
.wrapper {
  width: 1100px;
  margin: 50px auto;

  .content-container {
    max-width: 990px;
    margin: 0 auto;
    text-align: center;

    p {
      text-align: justify;
      width: 40%;
      margin: 0 auto;
    }

    .learn-more-btn {
      border: none;
      border-bottom: 2px solid tan;
      color: tan;
      cursor: pointer;
      font-size: 1.2em;
      margin-top: 25px;
    }
  }

  .main-img {
    width: 100%;
    height: 100vh;
    background: url("images/Art/WaveArt.jpg") center no-repeat;
    background-size: cover;
    margin: 50px 0;
  }

  // Main Image Content
  #about {
    text-align: center;

    h1 {
      margin: 50px auto;
    }

    .row {
      display: flex;

      .box {
        margin: 10px 20px;
        text-align: justify;
      }
    }
  }

  // Responsive About Section
  @media (max-width: 785px) {
    #about {
      .row {
        display: block;

        .box {
          text-align: center;
          margin: 20px 10px;

          p {
            text-align: justify;
          }
        }
      }
    }
  }
}

// Wrapper Respsonsive
@media (max-width: 897px) {
  .wrapper {
    width: 500px;
    .content-container {
      p {
        width: 90%;
      }
    }
  }
}

// Lower Mid Image
.lower-mid-img {
  width: 100%;
  height: 100vh;
  background: url("images/Art/Draw.jpg") center no-repeat;
  background-size: cover;
}

// Gallery Section
#gallery {
  width: 100%;
  height: auto;

  .gallery-container {
    .row {
      display: flex;
      justify-content: center;
    }

    .card {
      width: 100%;
      text-align: center;
      margin: 40px 35px;
      h3 {
        font-size: 2em;
        text-transform: uppercase;
        letter-spacing: 3px;
        margin: 15px auto;
      }

      .img {
        width: 90%;
        height: 30vh;
        border-radius: 10px;
        box-shadow: 0 0 10px 4px tan;
      }

      .img-1 {
        background: url("images/Art/Art4.jpg") center no-repeat;
        background-size: cover;
      }

      .img-2 {
        background: url("images/Art/Art5.jpg") center no-repeat;
        background-size: cover;
      }

      .img-3 {
        background: url("images/Art/Art6.jpg") center no-repeat;
        background-size: cover;
      }

      .img-4 {
        background: url("images/Art/Art3.jpg") center no-repeat;
        background-size: cover;
      }

      .img-5 {
        background: url("images/Art/Art1.jpg") center no-repeat;
        background-size: cover;
      }

      .img-6 {
        background: url("images/Art/Art2.jpg") center no-repeat;
        background-size: cover;
      }
    }
  }
}

// Responsive Gallery Section
@media (max-width: 877px) {
  #gallery {
    .gallery-container {
      .row {
        display: block;

        .card {
          h3 {
            display: none;
          }
        }
      }
    }
  }
}

// Register Section
#register {
  width: 800px;
  height: auto;
  margin: 20px auto;

  a {
    color: tan;
  }

  // Register
  .register-form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    .register-field {
      margin: 20px auto;
      border: none;
      border-bottom: 2px solid tan;
      text-transform: uppercase;
      text-align: center;
      letter-spacing: 3px;
      width: 50%;
    }

    .register-btn {
      padding: 10px 25px;
      cursor: pointer;
      width: 20%;
      margin: 10px auto;
      background: none;
      text-transform: uppercase;
      letter-spacing: 1.5px;
      border: none;
      border-bottom: 2px solid tan;
      &:hover {
        background: tan;
        transition: 0.5s;
        color: #fff;
      }
    }
  }
}

// Responsive Registration
@media (max-width: 875px) {
  #register {
    width: auto;
    height: 100vh;

    .register-form {
      text-align: center;
    }
  }
}

// Contact Container
#contact {
  width: 100%;
  height: auto;
  margin: 50px auto;
  text-align: center;

  .social-media {
    i {
      margin: 40px 20px;
      cursor: pointer;
      font-size: 3em;
      &:hover {
        color: tan;
        transition: 0.5s;
      }
    }
  }
}

// Contact Form
.contact-form {
  display: flex;
  justify-content: center;

  .email-field {
    text-transform: uppercase;
    border: none;
    border-bottom: 2px solid tan;
    background: none;
    color: #000;
    padding: 5px 25px;
    margin: 0 10px;
  }

  .contact-btn {
    border: none;
    text-transform: uppercase;
    border-bottom: 2px solid tan;
    font-size: 1.2em;
    cursor: pointer;
  }
}

// Footer
footer {
  height: 30vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: 3em;
    span {
      color: tan;
    }
  }
}
